/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Image, Input, Select, Tag, Divider, DatePicker } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined, PoundOutlined, CloseOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import Services from '../../../services/worker';
import dayjs from 'dayjs';
import moment from 'moment';
import UploadImage from '../../../utils/UploadImage';
import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';


export default function Worker() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [wid, setWid] = useState("")
    const [qData, setQData] = useState({ page: 1, limit: 20, fileType: 'photo' });
    const addNewModalRef = useRef();
    const addNewModalRef1 = useRef();
    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
        },
        {
            title: 'Phone',
            dataIndex: 'phone',
        },
        {
            title: 'Email',
            dataIndex: 'email',
        },
        {
            title: 'Photo',
            dataIndex: 'avatar',
            width: 100,
            render: (v) => {
                return <Image src={v} height={80} />
            }
        },
        {
            title: 'Payment Status',
            dataIndex: 'paymentStatus',
            render: (v, row) => {
                return <>
                    <Button type="primary" icon={<PoundOutlined />} onClick={() => {
                        setWid(row.id)
                        addNewModalRef1.current.openForm1(row.id)
                    }}>Payment Status</Button>
                </>
            }
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: 'id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this Service?"
                        onConfirm={() => { deleteData(row.id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        Services.listWorker(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v.id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        Services.deleteWorker(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, addNewModalRef1 }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>List of Worker</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
            <AddForm1 ref={addNewModalRef1} {...{ list, wid }} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, addNewModalRef1 }) {

    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
            qData[key] = "";
            list()
        }
    }

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name email contact" defaultValue={qData.key} onChange={e => onChange(e.target.value, "key")} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected images?. Be sure to delete the image from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [fileList, setFileList] = useState([]);


    const handleChange = (value) => {

        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });

    }


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { ...dt }
                    : { isEmailVerified: true }
            );
        }
    }));

    const save = () => {
        setAjxRequesting(true);
        Services.saveWorker(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }


    useEffect(() => {
        if (!open) {
            setData({ id: null });
        } else {
            setFileList([]);
        }
    }, [open]);

    useEffect(() => {
        handleChange({ file: fileList?.[0] });
    }, [fileList]);

    return (
        <>
            <Modal
                title={(!data.id ? 'Add' : 'Edit') + ' Worker'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={800}
                // className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={20}>
                                <Row gutter={[12, 2]}>
                                    <Col span={12}>
                                        <Form.Item label="Name" required>
                                            <Input placeholder="name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>

                                    <Col span={12}>
                                        <Form.Item label="Phone" required>
                                            <Input placeholder="Phone" type='number' value={data.phone} onChange={e => { handleChange({ phone: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label="Email" required>
                                            <Input placeholder="Email" type='email' value={data.email} onChange={e => { handleChange({ email: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label="Age" required>
                                            <Input placeholder="Age" type='number' value={data.age} onChange={e => { handleChange({ age: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>

                                    <Col span={12}>
                                        <Form.Item label="Gender" required>
                                            <Select value={data.gender} onChange={e => { handleChange({ gender: e }) }} >
                                                <Select.Option value={"Male"}>Male</Select.Option>
                                                <Select.Option value={'Female'}>Female</Select.Option>
                                                <Select.Option value={'Other'}>Other</Select.Option>
                                            </Select>
                                        </Form.Item>
                                    </Col>


                                </Row>
                            </Col>
                            <Col span={4}>
                                <Form.Item label="Avatar">
                                    <UploadImage
                                        value={data.avatar ? [data.avatar] : []}
                                        onChange={v => setData({ ...data, avatar: v[0] })}
                                        listType="picture-card"
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label=" Address">
                                    <Input.TextArea placeholder="Present Address" onChange={e => { handleChange({ address: e.target.value }) }} value={data.address} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Reference Phone" >
                                    <Input placeholder="Reference Phone" type='number' value={data.reference_contact} onChange={e => { handleChange({ reference_contact: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Insurance No." required>
                                    <Input placeholder="Insurance No." type='number' value={data.insuranceNo} onChange={e => { handleChange({ insuranceNo: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Job Type" required>
                                    <Select value={data.jobType} onChange={e => { handleChange({ jobType: e }) }} >
                                        <Select.Option value={"Full-time"}>Full-Time</Select.Option>
                                        <Select.Option value={'Half-time'}>Half-Time</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Employment Type" required>
                                    <Select value={data.employmentType} onChange={e => { handleChange({ employmentType: e }) }} >
                                        <Select.Option value={"Student"}>Student</Select.Option>
                                        <Select.Option value={'Employee'}>Employee</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Divider orientation="left">Bank Details</Divider>
                            <Col span={12}>
                                <Form.Item label="Bank Name" required>
                                    <Input placeholder="Bank Name" value={data.bank_name} onChange={e => { handleChange({ bank_name: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Bank Branch Name" required>
                                    <Input placeholder="Bank Branch Name" value={data.bank_branch_name} onChange={e => { handleChange({ bank_branch_name: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Account Holder Name" required>
                                    <Input placeholder="Account Holder Name" value={data.account_holder_name} onChange={e => { handleChange({ account_holder_name: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Account No." required>
                                    <Input placeholder="Account No." value={data.accountNo} onChange={e => { handleChange({ accountNo: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="IFSC Code" required>
                                    <Input placeholder="IFSC Code" value={data.ifsc_code} onChange={e => { handleChange({ ifsc_code: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Divider />
                            <Col span={12}>
                                <Form.Item label="Passport No." required>
                                    <Input placeholder="Passport No." value={data.passportNo} onChange={e => { handleChange({ passportNo: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Visa Type" required>
                                    <Select value={data.visa_type} onChange={e => { handleChange({ visa_type: e }) }} >
                                        <Select.Option value={"Tourist"}>Tourist</Select.Option>
                                        <Select.Option value={'Work-visit'}>Work-visit</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Emigration Type" required>
                                    <Select value={data.emigration_type} onChange={e => { handleChange({ emigration_type: e }) }} >
                                        <Select.Option value={"ECR"}>ECR</Select.Option>
                                        <Select.Option value={'NON-ECR'}>NON-ECR</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12}></Col>
                            <Col span={8}>
                                <Form.Item label="Passport Image">
                                    <UploadImage
                                        value={data.passport_picture ? [data.passport_picture] : []}
                                        onChange={v => setData({ ...data, passport_picture: v[0] })}
                                        listType="picture-card"
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="Driving License Image">
                                    <UploadImage
                                        value={data.driving_license_picture ? [data.driving_license_picture] : []}
                                        onChange={v => setData({ ...data, driving_license_picture: v[0] })}
                                        listType="picture-card"
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="Visa Image">
                                    <UploadImage
                                        value={data.visa_picture ? [data.visa_picture] : []}
                                        onChange={v => setData({ ...data, visa_picture: v[0] })}
                                        listType="picture-card"
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label={data?._id ? "Update Password" : "Set Password"} required={data?._id ? false : true}>
                                    <Input type='password' value={data?.password} onChange={e => { handleChange({ password: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal >
        </>
    );
});
const AddForm1 = forwardRef((props, ref) => {
    const { list, wid } = props;
    const [open1, setOpen1] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [fileList, setFileList] = useState([]);

    console.log(wid, 'qwertyui');
    const handleChange = (v, k) => {
        let varDt = data;
        let keys = k?.split('.');
        for (let i = 0; i < keys?.length; i++) {
            if (i + 1 === keys.length) {
                varDt[keys[i]] = v;
            } else {
                if (typeof varDt[keys[i]] === 'undefined') {
                    if (parseInt(keys[i + 1]) * 1 >= 0) {
                        varDt[keys[i]] = [];
                    } else {
                        varDt[keys[i]] = {};
                    }
                } varDt = varDt[keys[i]];
            }
        }
        setData({ ...data, ...v });
    }


    useImperativeHandle(ref, () => ({
        openForm1(dt) {
            setOpen1(true);
            setData(
                dt
                    ? { _id: dt }
                    : { isEmailVerified: true, workerId: wid }
            );
        }
    }));
    useEffect(() => {
        console.log(data, "wowwwwwwwwwwwwwwwwwwwwwwwwww");
    }, [data])

    const getDetails = () => {
        setAjxRequesting(true);
        Services.earnDetail({ workerId: data._id }).then(res => {
            setData(res.data || {});
            // setFileList(res.data?.imgUrl ? [res.data?.imgUrl] : []);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }




    const save = (dt) => {
        console.clear();
        console.log(111, dt);
        dt = !dt ? data : dt;
        console.log("HHHH", dt);
        // return;
        setAjxRequesting(true);
        Services.saveEarn(dt).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            // setOpen1(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

    useEffect(() => {
        if (!open1) {
            setData({ id: null });
        } else {
            setData({ workerId: data._id });
        }
    }, [open1]);

    return (
        <>
            <Modal
                title={'Worker Payment Status'}
                style={{ top: 20 }}
                open={open1}
                // okText="Save"
                // onOk={save}
                // okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen1(false); }}
                destroyOnClose
                maskClosable={false}
                width={1200}
                // className="app-modal-body-overflow"
                footer={[
                    <Typography.Title level={4} style={{ textAlign: 'center' }}>Total Earning:-  ${data?.totalEarning}</Typography.Title>,
                    <Button key="cancel" onClick={() => { setOpen1(false); }}>Cancel</Button>,
                    // <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>

                            <Col span={24}>
                                <Form.Item>
                                    <Variant {...{ variantDetails: data.earnings, parentKay: 'earnings', handleChange, completeData: data, save }} />
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal >
        </>
    );
});

function Variant({ variantDetails: data, parentKay, handleChange, completeData: newData, save }) {
    console.log(parentKay, "dfghjk");
    return (
        <React.Fragment>
            <Divider orientation="left">{parentKay === 'variants' ? "Payemnt Status" : "Payemnt Status"}</Divider>
            <Table
                {...{
                    bordered: false,
                    size: 'small',
                    showHeader: true,
                    footer: null,
                    tableLayout: undefined,
                }}
                // handleChange( { `${parentKay}.${i}.paymentStatus`: e } )

                columns={
                    [
                        {
                            title: 'Booking Name',
                            dataIndex: 'booking',
                            width: 300,
                            render: (v, row, i) => <span>{v?.apartmentId?.type}</span>
                        },
                        {
                            title: 'Payment Status',
                            dataIndex: 'paymentStatus',
                            width: 300,
                            render: (v, row, i) => {
                                return <>
                                    <Select placeholder="Payment Success/Pending" value={v} onChange={(e) => { handleChange((e), `${parentKay}.${i}.paymentStatus`) }}>
                                        <Select.Option value="paid">Paid</Select.Option>
                                        <Select.Option value="unpaid">Unpaid</Select.Option>
                                    </Select>
                                </>
                            },
                        },
                        {
                            title: 'Action',
                            // dataIndex: 'price',
                            width: 100,
                            render: (v, row, i) => {
                                return <>
                                    <Button type="primary" onClick={() => {
                                        console.log("data---", data?.find(vv => vv.id == v.id))
                                        save(data?.find(vv => vv.id == v.id));
                                    }}>Save</Button>
                                </>
                            }
                        }
                    ]
                }
                dataSource={data?.length ? data?.map((v, i) => ({ ...v, key: i })) : []}
            />
        </React.Fragment>
    );
}