/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Image, Input, Select, Tag } from 'antd';
import { DeleteOutlined, LoadingOutlined, SendOutlined, NotificationOutlined, WarningOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import Services from '../../services/notification'
import TinyMce from '../../utils/TinyMce';
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';
import HTMLRenderer from 'react-html-renderer'


export default function Notification() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, });
    const addNewModalRef = useRef();
    const columns = [
        {
            title: 'Title',
            dataIndex: 'title',
            width:200
        },
        {
            title: 'Message',
            dataIndex: 'description',
            render:(v) => (<HTMLRenderer html={v}></HTMLRenderer>)
        },
        // {
        //     title: 'User',
        //     dataIndex: 'type',
        // },
        // {
        //     title: 'Offer',
        //     dataIndex: 'type',
        // },
        // {
        //     title: '',
        //     dataIndex: 'type',
        // },
        // {
        //     title: 'Type',
        //     dataIndex: 'type',
        // },
        // {
        //     title: 'Status',
        //     dataIndex: 'status',
        //     width: 80,
        //     render: isActive => {
        //         if (isActive) {
        //             return <Tag color='green'>Active</Tag>
        //         } else {
        //             return <Tag color='red'>Inactive</Tag>
        //         }
        //     },
        // },
        // {
        //     title: 'Action',
        //     dataIndex: 'id',
        //     width: 70,
        //     render: (v, row) => {
        //         return <>
        //             <Popconfirm
        //                 title="Are you sure to delete this Service?"
        //                 onConfirm={() => { deleteData(row.id); }}
        //                 okText="Yes"
        //                 cancelText="No"
        //             >
        //                 <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
        //             </Popconfirm>
        //             <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row.id) }} />
        //         </>
        //     }
        // },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        Services.listFloor(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v.id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        Services.deleteFloor(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        // rowSelection: {
        //     onChange: (selectedRowKeys) => {
        //         setSelected(selectedRowKeys);
        //     },
        // },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>Notification List</Typography.Title>
            <Table
                {...tableProps}
                pagination={{position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list }) {

    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
            qData[key] = "";
            list()
        }
    }

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by title" value={qData.key} onChange={e => onChange(e.target.value,"key")} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<SendOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Send New Notification </Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected images?. Be sure to delete the image from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);


    const handleChange = (value) => {

        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });

    }


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { id: dt }
                    : {}
            );
        }
    }));

    // const getDetails = () => {
    //     setAjxRequesting(true);
    //     Services.detailFloor(data.id).then(res => {
    //         setData(res.data || {});
    //     }).catch(err => {
    //         AntdMsg(err.message, 'error');
    //     }).finally(() => {
    //         setAjxRequesting(false);
    //     });
    // }

    const save = () => {
        setAjxRequesting(true);
        Services.saveFloor(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }

    // useEffect(() => {
    //     if (data.id) {
    //         getDetails();
    //     }
    // }, [data.id]);

    useEffect(() => {
        if (!open) {
            setData({ id: null });
        }
    }, [open]);

    return (
        <>
            <Modal
                title={'Send New Notification'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={800}
                // className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" icon={<WarningOutlined />}  onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" icon={<NotificationOutlined />} onClick={save}>Send Notification</Button>
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Form.Item label="Title" required>
                                    <Input placeholder="User Name" value={data.title} onChange={e => { handleChange({ title: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            {/* <Col span={12}>
                                <Form.Item label="Offer" required>
                                    <Input placeholder="Offer" value={data.type} onChange={e => { handleChange({ type: e.target.value }) }} />
                                </Form.Item>
                            </Col> */}
                            <Col span={24}>
                                <Form.Item label="Description" required>
                                    <TinyMce initialValue={data.description} onChange={(v) => { data.description = v; }} />
                                </Form.Item>
                            </Col>
                            {/* <Col span={24}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col> */}
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});