import axios from "../utils/axios";

export default class service {

    static baseURL = 'booking';

    static detailBooking(id, data) {
        return axios.get(this.baseURL + '/detail/' + id, { params: data });
    }
    static listBooking(data) {
        return axios.get(this.baseURL + '/list', { params: data });
    }
    static saveWorker(data, query) {
        return axios.post("/job-assign/assign", data, { params: query });
    }

}