import axios from "../utils/axios";

export default class service {

    static baseURL = 'worker';

    static detailWorker(id, data) {
        return axios.get(this.baseURL + '/detail/' + id, { params: data });
    }
    static listWorker(data) {
        return axios.get(this.baseURL + '/list', { params: data });
    }
    static saveWorker(data, query) {
        return axios.post(this.baseURL + "/save", data, { params: query });
    }
    static deleteWorker(id) {
        return axios.post(this.baseURL + '/delete', { ids: id });
    }
    static earnDetail(data) {
        return axios.get( '/worker-earn/list', { params: data });
    }
    static saveEarn(data, query) {
        return axios.post("/worker-earn/paid", data, { params: query });
    }

}