/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag, DatePicker } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';

import service from '../../services/voucher';
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';
import dayjs from 'dayjs';
import moment from 'moment';
import util from '../../utils/util';

export default function Voucher() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, });
    const addNewModalRef = useRef();
    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
        },
        {
            title: 'Code',
            width: 100,
            dataIndex: 'code',
        },
        {
            title: 'EndDate',
            width: 100,
            dataIndex: 'expiryDate',
            render: (v, row) => <span>{moment(v).format('DD MMM YYYY')}</span>
        },
        {
            title: "Discount Value",
            dataIndex: "discountValue",
            width: 120,
            render: (v, row) => row.discountType === 'flat' ? <b> {v}</b> : <b>{v} %</b>
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 60,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: 'id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this User?"
                        onConfirm={() => { deleteData(row.id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        service.listVoucher(qData).then(res => {
            setData(res.data?.map((v) => ({ key: v.id, ...v })).reverse());
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        service.deleteVoucher(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>List of Voucher</Typography.Title>
            <Table
                {...tableProps}
                pagination={{position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, }) {

    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
            qData[key] = "";
            list()
        }
    }
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name" defaultValue={qData.key} onChange={e => onChange(e.target.value, "key")} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected images?. Be sure to delete the image from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }
                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);

    const handleChange = (value) => {

        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { ...dt }
                    : { type: 'percent', status: true }
            );
        }
    }));

    const save = () => {
        setAjxRequesting(true);
        service.saveVoucher(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    useEffect(() => {
        if (!data.id) {
            handleChange({ code: util.removeSpecialChars(data?.name)?.toUpperCase() });
        }
    }, [data.name]);

    return (
        <>
            <Modal
                title={(!data.id ? 'Add' : 'Edit') + ' Voucher'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={800}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>

                            <Col span={12}>
                                <Form.Item label="Name" required>
                                    <Input placeholder="name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Code" required>
                                    <Input placeholder="Code" value={data.code} onChange={e => { handleChange({ code: util.removeSpecialChars(e.target.value)?.toUpperCase() }) }} />
                                </Form.Item>
                            </Col>
                             <Col span={6}>
                                <Form.Item label="Discount Type" required>
                                    <Select value={data.type} onChange={e => { handleChange({ type: e }) }} >
                                        <Select.Option value={'percent'}>Percent</Select.Option>
                                        <Select.Option value={'flat'}>Flat</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item label="Discount Value" required>
                                    <Input type='number' placeholder="Discount Value" addonBefore={data.type === 'percent' ? '%' : ''} value={data.discountValue}
                                        onChange={e => {
                                            if (data.type === 'flat') {
                                                handleChange({ discountValue: e.target.value, maxDiscountValue: e.target.value })
                                            } else {

                                                handleChange({ discountValue: e.target.value })
                                            }
                                        }} />
                                </Form.Item>
                            </Col>
                            {(data.type === "flat") ?
                                <Col span={6}>
                                    <Form.Item label="Max Discount Value" >
                                        <Input type='number' placeholder="Max Discount Price" addonBefore="" value={data.maxDiscountValue} onChange={e => { handleChange({ maxDiscountValue: e.target.value }) }} disabled />
                                    </Form.Item>
                                </Col>
                                :
                                <Col span={6}>
                                    <Form.Item label="Max Discount Value" >
                                        <Input type='number' placeholder="Max Discount Price" addonBefore="" value={data.maxDiscountValue} onChange={e => { handleChange({ maxDiscountValue: e.target.value }) }} />
                                    </Form.Item>
                                </Col>
                            }

                            <Col span={6}>
                                <Form.Item label="Minimun Order Value">
                                    <Input type='number' placeholder="Minimun Order Price" addonBefore="" value={data.minOrderPrice} onChange={e => { handleChange({ minOrderPrice: e.target.value }) }} />
                                </Form.Item>
                            </Col>

                            <Col span={8}>
                                <Form.Item label="Expiry Date" required>
                                    <DatePicker value={data.expiryDate && dayjs(moment(data.expiryDate).format("YYYY-MM-DD"))}
                                        format='DD MMM YYYY'
                                        disabledDate={(current) => current.isBefore(moment(new Date()).subtract(1, "day"))}
                                        onChange={(e) => { handleChange({ expiryDate: new Date(e) }) }} style={{ width: '100%' }} />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});