/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Image, Input, Select, Tag } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import Services from '../../../services/quiz';

import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';


export default function QuizAnswer({questionId}) {

    const [data, setData] = useState([]);
    const [quiz, setQuiz] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20,questionId });
    const addNewModalRef = useRef();
    const columns = [
        {
            title: 'Answer',
            dataIndex: 'quizOption',
            render: (v) => {
                return <p>{v.option}</p>
            }
        },
        // {
        //     title: 'Status',
        //     dataIndex: 'status',
        //     width: 80,
        //     render: isActive => {
        //         if (isActive) {
        //             return <Tag color='green'>Active</Tag>
        //         } else {
        //             return <Tag color='red'>Inactive</Tag>
        //         }
        //     },
        // },
        {
            title: 'Action',
            dataIndex: 'id',
            width: 70,
            render: (v, row) => {
                return <>
                    {/* <Popconfirm
                        title="Are you sure to delete this Service?"
                        onConfirm={() => { deleteData(row.id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm> */}
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        Services.listQuizAnswer(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v.id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    function listOptions() {
        Services.listQuizOption({questionId})
            .then(res => { setQuiz(res.data || []); }).catch(err => { AntdMsg('Option data not loaded', 'error'); });
    }

    const deleteData = (id) => {
        // Services.deleteAddress(id).then(res => {
        //     AntdMsg(res.message);
        //     list();
        //     setSelected([]);
        // }).catch(err => {
        //     AntdMsg(err.message, 'error');
        // })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list,data }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
        listOptions()
    }, [qData.page, qData.limit]);

    return (
        <>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list,questionId,quiz}} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list,data }) {

    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
            qData[key] = "";
            list()
        }
    }
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    {/* <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or code" value={qData.key} onChange={e => onChange(e.target.value, "key")} allowClear />
                    </Form.Item> */}
                </Col>
                <Col span={3}>
                    {/* <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item> */}
                </Col>
                <Col span={5}>
                    {
                        data?.length !== 1 ?
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item> : ""
                    }
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected images?. Be sure to delete the image from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list,questionId,quiz } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [fileList, setFileList] = useState([]);


    const handleChange = (value) => {

        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });

    }


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { ...dt }
                    : {quizQuestionId: questionId }
            );
        }
    }));

    const save = () => {
        setAjxRequesting(true);
        Services.saveQuizAnswer(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }

    useEffect(() => {
        if (!open) {
            setData({ id: null });
        } else {
            setFileList([]);
        }
    }, [open]);

    useEffect(() => {
        handleChange({ file: fileList?.[0] });
    }, [fileList]);

    return (
        <>
            <Modal
                title={(!data.id ? 'Add' : 'Edit') + ' Quiz Answer'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={500}
                // className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                        <Col span={24}>
                        <Form.Item label="Choose Answer" >
                                    <Select value={data.quizOptionId} onChange={v => { handleChange({ quizOptionId: v }) }}
                                        options={
                                            quiz.map(v => (
                                                {
                                                    value: v.id,
                                                    label: <>
                                                        <p style={{ padding: "0px", margin: "0px" }}>{v?.option}</p>
                                                    </>,
                                                }
                                            ))
                                        } >
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});