import axios from "../utils/axios";

export default class service {

    static baseURL = 'quiz';

    /// quiz video
    static listQuizVideo(data) {
        return axios.get(this.baseURL + '/video/get-quizzes', { params: data });
    }
    static saveQuizVideo(data, query) {
        return axios.post(this.baseURL + "/video/save", data, { params: query });
    }
    static deleteQuizVideo(id) {
        return axios.post(this.baseURL + '/video/delete', { ids: id });
    }

    //// quiz question 
    static listQuizQuestion(data) {
        return axios.get(this.baseURL + '/question/list', { params: data });
    }
    static saveQuizQuestion(data, query) {
        return axios.post(this.baseURL + "/question/save", data, { params: query });
    }
    static deleteQuizQuestion(id) {
        return axios.post(this.baseURL + '/question/delete', { ids: id });
    }
    //// quiz options 
    static listQuizOption(data) {
        return axios.get(this.baseURL + '/options/list', { params: data });
    }
    static saveQuizOption(data, query) {
        return axios.post(this.baseURL + "/options/save", data, { params: query });
    }
    //// quiz answer 
    static listQuizAnswer(data) {
        return axios.get(this.baseURL + '/answer/list', { params: data });
    }
    static saveQuizAnswer(data, query) {
        return axios.post(this.baseURL + "/answer/save", data, { params: query });
    }
}