import axios from "../utils/axios";

export default class service {

    static baseURL = 'notification';

    static detailFloor(id, data) {
        return axios.get(this.baseURL + '/detail/' + id, { params: data });
    }
    static listFloor(data) {
        return axios.get(this.baseURL + '/get-notifications', { params: data });
    }
    static saveFloor(data, query) {
        return axios.post(this.baseURL + "/push-notification", data, { params: query });
    }
    static deleteFloor(id) {
        return axios.post(this.baseURL + '/delete', { ids: id });
    }

}