import { DashboardOutlined, BankOutlined, ApartmentOutlined, CalendarOutlined } from '@ant-design/icons';

import Dashboard from '../pages/Dashboard';
import Service from '../pages/Service';
import Addon from '../pages/Addon';
import Application from '../pages/Application';
import CustomerCare from '../pages/CustomerCare';
import FAQ from '../pages/FAQ';
import Apartment from '../pages/Apartment';
import Floor from '../pages/Floor';
import Membership from '../pages/Membership';
import User from '../pages/management/User';
import Worker from '../pages/management/Worker';
import Subscription from '../pages/Subscription';
import Reviews from '../pages/Reviews';
import Voucher from '../pages/Voucher';
import Booking from '../pages/Booking';
import Banner from '../pages/Banner';
import QuizVideo from '../pages/quiz/QuizVideo';
import QuizQuestion from '../pages/quiz/QuizQuestion';
import Notification from '../pages/notification';
// import PaymentStatus from '../pages/paymentStatus';

const routes = [
    { title: 'Dashboard', icon: DashboardOutlined, url: '/', component: Dashboard },
    { title: 'Service', icon: ApartmentOutlined, url: '/service', component: Service },
    { title: 'Addon', icon: ApartmentOutlined, url: '/addon', component: Addon },
    { title: 'Membership', icon: ApartmentOutlined, url: '/membership', component: Membership },
    { title: 'Subscription', icon: ApartmentOutlined, url: '/subscription', component: Subscription },
    { title: 'Banner', icon: ApartmentOutlined, url: '/banner', component:Banner },
    { title: 'Reviews', icon: ApartmentOutlined, url: '/reviews', component:Reviews },
    { title: 'Voucher', icon: ApartmentOutlined, url: '/voucher', component:Voucher },
    { title: 'Bookings', icon: ApartmentOutlined, url: '/bookings', component:Booking },
    { title: 'Notification', icon: ApartmentOutlined, url: '/Nntification', component:Notification },
    { title: 'Home', icon: ApartmentOutlined, url: '/apartment', children:[

        { title: 'Apartment', icon: ApartmentOutlined, url: '/', component: Apartment },
        { title: 'Floor', icon: ApartmentOutlined, url: '/floor', component: Floor },
    ] },
    { title: 'Quiz', icon: ApartmentOutlined, url: '/quiz-video', children:[

        { title: 'Quiz Video', icon: ApartmentOutlined, url: '/', component: QuizVideo },
        { title: 'Quiz Question', icon: ApartmentOutlined, url: '/question', component:QuizQuestion  },
    ] },
    { title: 'Management', icon: ApartmentOutlined, url: '/user', children:[

        { title: 'User', icon: ApartmentOutlined, url: '/', component: User },
        { title: 'Worker', icon: ApartmentOutlined, url: '/worker', component: Worker },
    ] },
    { title: 'Application', icon: ApartmentOutlined, url: '/application', children:[
        { title: 'Application', icon: ApartmentOutlined, url: '/', component: Application },
        { title: 'CustomerCare', icon: ApartmentOutlined, url: '/customer-care', component: CustomerCare},
        { title: 'FAQ', icon: ApartmentOutlined, url: '/faq', component: FAQ},
    ] },

].filter(v => (!v.hidden) || v.children?.length);

export default routes;