import axios from "../utils/axios";

export default class service {

    static baseURL = 'user';

    // static detailBanner(id, data) {
    //     return axios.get(this.baseURL + '/detail/' + id, { params: data });
    // }
    static listAddress(data) {
        return axios.get(this.baseURL + '/address/list', { params: data });
    }
    static saveAddress(data, query) {
        return axios.post(this.baseURL + "/address/save", data, { params: query });
    }
    static deleteAddress(id) {
        return axios.post(this.baseURL + '/address/delete', { ids: id });
    }

}